import { useState, useRef, useEffect } from "react";
import { Canvas, useFrame } from "@react-three/fiber";
import { Sphere } from "@react-three/drei";
import * as THREE from "three";

/* ==================== 맑음 ==================== */
const Sun = ({ position, scale }) => {
  const groupRef = useRef();
  const capsulePositions = [
    { position: [0, 2.8, 0], rotation: [0, Math.PI / 2, 0] },
    { position: [2, 2, 0], rotation: [0, 0, -Math.PI / 4] },
    { position: [2.8, 0, 0], rotation: [Math.PI / 2, 0, Math.PI / 2] },
    { position: [2, -2, 0], rotation: [0, 0, Math.PI / 4] },
    { position: [0, -2.8, 0], rotation: [0, Math.PI / 2, 0] },
    { position: [-2, -2, 0], rotation: [0, 0, -Math.PI / 4] },
    { position: [-2.8, 0, 0], rotation: [Math.PI / 2, 0, Math.PI / 2] },
    { position: [-2, 2, 0], rotation: [0, 0, Math.PI / 4] },
  ];
  useFrame(() => {
    groupRef.current.rotation.z += 0.01;
  });
  return (
    <group ref={groupRef} position={position} scale={scale}>
      {capsulePositions.map((params, index) => (
        <mesh key={index} position={params.position} rotation={params.rotation}>
          <capsuleGeometry args={[0.2, 0.8, 1.6, 16, 8]} />
          <meshStandardMaterial metalness={1} roughness={0.8} color="#FF6347" />
        </mesh>
      ))}
      <mesh position={[0, 0, 0]}>
        <sphereGeometry args={[1.7, 16, 32]} />
        <meshStandardMaterial metalness={1} roughness={0.8} attach="material" color="#FF6347" />
      </mesh>
    </group>
  );
};
/* ==================== 구름모델 ==================== */
const CloudModel = ({ position }) => {
  return (
    <group position={position}>
      <mesh position={[-1.8, -0.4, 0]}>
        <sphereGeometry args={[1.25, 16, 32, 0, Math.PI * 2, 0, Math.PI * 0.7]} />
        <meshStandardMaterial metalness={1} roughness={0.8} attach="material" color={"#fff"} />
      </mesh>
      <mesh position={[-0.9, -0.7, 1]}>
        <sphereGeometry args={[0.75, 16, 32, 0, Math.PI * 2, 0, Math.PI * 0.7]} />
        <meshStandardMaterial metalness={1} roughness={0.8} attach="material" color={"#fff"} />
      </mesh>
      <mesh position={[1, 0.1, 0]}>
        <sphereGeometry args={[2, 16, 32, 0, Math.PI * 2, 0, Math.PI * 0.7]} />
        <meshStandardMaterial metalness={1} roughness={0.8} attach="material" color={"#fff"} />
      </mesh>
    </group>
  );
};
/* ==================== 구름 ==================== */
const Cloud = () => {
  return (
    <group>
      <Sun position={[-1.75, 0.5, -0.5]} scale={[0.7, 0.7, 0.7]} />
      <CloudModel position={[0.25, -0.75, 0]} />
    </group>
  );
};
/* ==================== 흐림 ==================== */
const Clouds = () => {
  return (
    <group>
      <CloudModel position={[-1, 0.5, 0]} />
      <CloudModel position={[1, -0.5, 0]} />
    </group>
  );
};
/* ==================== 물방울모델 ==================== */
const WaterModel = ({ position }) => {
  useFrame(() => {
    // groupRef.current.position.y -= 0.02;
  });
  return (
    <group position={position}>
      <mesh position={[0, -1.7, 0]}>
        <coneGeometry args={[0.25, 0.5, 32]} />
        <meshStandardMaterial metalness={1} roughness={0.8} attach="material" color={"#09f"} />
      </mesh>
      <mesh position={[0, -2, 0]}>
        <sphereGeometry args={[0.25, 32, 32]} />
        <meshStandardMaterial metalness={1} roughness={0.8} attach="material" color={"#09f"} />
      </mesh>
    </group>
  );
};
/* ==================== 비 ==================== */
const Rain = () => {
  // const groupRef = useRef();
  useFrame(() => {
    // groupRef.current.position.y -= 0.02;
  });
  return (
    <group>
      <CloudModel position={[0, 0.25, 0]} />
      <WaterModel position={[-1.5, 0, 0]} />
      <WaterModel position={[0, 0, 0]} />
      <WaterModel position={[1.5, 0, 0]} />
    </group>
  );
};
/* ==================== 눈 ==================== */
const Snow = () => {
  const groupRef = useRef();
  const capsulePositions = [
    { position: [0, 0, 0], rotation: [0, Math.PI / 2, 0] },
    { position: [0, 0, 0], rotation: [0, 0, -Math.PI / 3] },
    { position: [0, 0, 0], rotation: [0, 0, Math.PI / 3] },
  ];
  const branchCapsulePositions = [
    { position: [-0.375, 1.875, 0], rotation: [0, 0, Math.PI / 3] },
    { position: [0.375, 1.875, 0], rotation: [0, 0, -Math.PI / 3] },
    { position: [1.375, 1.25, 0], rotation: [0, Math.PI / 2, 0] },
    { position: [1.75, 0.5, 0], rotation: [0, 0, Math.PI / 3] },
    { position: [1.375, -1.25, 0], rotation: [0, Math.PI / 2, 0] },
    { position: [1.75, -0.5, 0], rotation: [0, 0, -Math.PI / 3] },
    { position: [-0.375, -1.875, 0], rotation: [0, 0, -Math.PI / 3] },
    { position: [0.375, -1.875, 0], rotation: [0, 0, Math.PI / 3] },
    { position: [-1.375, -1.25, 0], rotation: [0, Math.PI / 2, 0] },
    { position: [-1.75, -0.5, 0], rotation: [0, 0, Math.PI / 3] },
    { position: [-1.375, 1.25, 0], rotation: [0, Math.PI / 2, 0] },
    { position: [-1.75, 0.5, 0], rotation: [0, 0, -Math.PI / 3] },
  ];
  useFrame(() => {
    groupRef.current.rotation.z += 0.01;
  });
  return (
    <group ref={groupRef}>
      {capsulePositions.map((params, index) => (
        <mesh key={index} position={params.position} rotation={params.rotation}>
          <capsuleGeometry args={[0.25, 5, 1.6, 16, 8]} />
          <meshStandardMaterial metalness={1} roughness={0.8} color="#c9f" />
        </mesh>
      ))}
      {branchCapsulePositions.map((params, index) => (
        <mesh key={index} position={params.position} rotation={params.rotation}>
          <capsuleGeometry args={[0.25, 0.5, 1.6, 16, 8]} />
          <meshStandardMaterial metalness={0.5} roughness={0.4} color="#c9f" />
        </mesh>
      ))}
    </group>
  );
};

function WeatherModel({ skyState }) {
  return (
    <Canvas>
      {/* 빛 */}
      <ambientLight intensity={1} />
      <pointLight position={[-10, 10, 10]} intensity={2000} />
      {/* 구름 */}
      {skyState === "sun" ? (
        <Sun />
      ) : skyState === "cloud" ? (
        <Cloud />
      ) : skyState === "clouds" ? (
        <Clouds />
      ) : skyState === "rain" ? (
        <Rain />
      ) : (
        <Snow />
      )}
    </Canvas>
  );
}

export default WeatherModel;
